	module.exports = {"data":{"markdownRemark":{"html":"<p>Your Security Page can list the top 5 hackers (ranked by reputation) that disclosed vulnerabilities to your program.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/top-hackers-1a-e69bf0c13029e70499bebfa41bd672b1-bedd4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 470px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 151.91489361702128%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"top-hackers-1\"\n        title=\"\"\n        src=\"/static/top-hackers-1a-e69bf0c13029e70499bebfa41bd672b1-bedd4.png\"\n        srcset=\"/static/top-hackers-1a-e69bf0c13029e70499bebfa41bd672b1-53242.png 125w,\n/static/top-hackers-1a-e69bf0c13029e70499bebfa41bd672b1-2b311.png 250w,\n/static/top-hackers-1a-e69bf0c13029e70499bebfa41bd672b1-bedd4.png 470w\"\n        sizes=\"(max-width: 470px) 100vw, 470px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Hackers that earn reputation for a resolved report are automatically recognized and given Thanks on your program. Users viewing the Top Hackers section on your security page can click on <b>All Hackers</b> to be directed to your <b>Thanks</b> page.</p>\n<h3>Program Thanks Page</h3>\n<p>The Thanks page shows all hackers that have submitted a resolved report to your program.  It’s a continued list from the <b>Top hackers</b> list on your Security Page. Giving Thanks to hackers happens automatically for every report where reputation is earned. All hackers will receive Thanks when their report is resolved.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/top-hackers-2a-bc7b66dab40bf659eb8f763ca0540a1b-f8619.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.2127659574468%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"top-hackers-2\"\n        title=\"\"\n        src=\"/static/top-hackers-2a-bc7b66dab40bf659eb8f763ca0540a1b-2f183.png\"\n        srcset=\"/static/top-hackers-2a-bc7b66dab40bf659eb8f763ca0540a1b-67829.png 125w,\n/static/top-hackers-2a-bc7b66dab40bf659eb8f763ca0540a1b-5c59f.png 250w,\n/static/top-hackers-2a-bc7b66dab40bf659eb8f763ca0540a1b-2f183.png 500w,\n/static/top-hackers-2a-bc7b66dab40bf659eb8f763ca0540a1b-c93e8.png 750w,\n/static/top-hackers-2a-bc7b66dab40bf659eb8f763ca0540a1b-4e628.png 1000w,\n/static/top-hackers-2a-bc7b66dab40bf659eb8f763ca0540a1b-b3ac4.png 1500w,\n/static/top-hackers-2a-bc7b66dab40bf659eb8f763ca0540a1b-f8619.png 1974w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Hacker Thanks Page</h3>\n<p>Hackers also have their own Thanks page on their profile where all of the companies they’ve submitted valid reports to are listed. The programs are listed by the hacker’s reputation for each program. The Thanks page is automatically updated after a report is resolved and reputation is gained.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/top-hackers-3-13f07455de2098fa41ff07c8f8843de7-9a261.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 78.04597701149424%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"top-hackers-3\"\n        title=\"\"\n        src=\"/static/top-hackers-3-13f07455de2098fa41ff07c8f8843de7-2f183.png\"\n        srcset=\"/static/top-hackers-3-13f07455de2098fa41ff07c8f8843de7-67829.png 125w,\n/static/top-hackers-3-13f07455de2098fa41ff07c8f8843de7-5c59f.png 250w,\n/static/top-hackers-3-13f07455de2098fa41ff07c8f8843de7-2f183.png 500w,\n/static/top-hackers-3-13f07455de2098fa41ff07c8f8843de7-c93e8.png 750w,\n/static/top-hackers-3-13f07455de2098fa41ff07c8f8843de7-4e628.png 1000w,\n/static/top-hackers-3-13f07455de2098fa41ff07c8f8843de7-b3ac4.png 1500w,\n/static/top-hackers-3-13f07455de2098fa41ff07c8f8843de7-9a261.png 1740w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/programs/top-hackers.html","id":"programs/top-hackers","title":"Top Hackers","description":null}}},"pathContext":{}}

